.visualizer {
  z-index: -1;
  position: fixed;
  inset: 0;
}

.player {
  --tonearm-turn-duration: 1.5s;
  background: #666;
  border-radius: 1rem;
  justify-content: space-between;
  align-items: flex-start;
  width: 18.75rem;
  height: 13.75rem;
  margin-left: -9.375rem;
  display: flex;
  position: fixed;
  bottom: .625rem;
  left: 50%;
  box-shadow: .125rem .125rem .625rem #333;
}

.player:before {
  content: "";
  background: #333;
  border-radius: 50%;
  width: 11.22rem;
  height: 11.22rem;
  display: block;
  position: absolute;
  inset: 50% auto auto 50%;
  transform: translate(-50%, -50%);
}

.player__tonearm {
  content: "";
  transform-origin: 100%;
  width: 10rem;
  height: .625rem;
  transition: transform var(--tonearm-turn-duration) ease-in-out;
  z-index: 3;
  background: #454545;
  border-radius: .125rem;
  margin: 1.25rem 1.75rem;
  display: block;
  position: relative;
  transform: rotate(-95deg);
}

.player_playing .player__tonearm {
  transform: rotate(-55deg);
}

.player_changing-record .player__tonearm {
  z-index: 1;
}

.player__button {
  color: #666;
  background: #333;
  border: none;
  border-radius: .25rem;
  width: 1.5rem;
  height: 1.5rem;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.player__button:enabled {
  cursor: pointer;
}

.player__button:disabled {
  opacity: .5;
}

.player__controls {
  z-index: 3;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  gap: 1.75rem;
  height: 100%;
  padding: 1rem;
  display: flex;
  position: relative;
}

.player_changing-record .player__controls {
  z-index: 1;
}

.player__playlist {
  z-index: 2;
  transform-origin: center;
  transition: transform var(--change-record-animation-duration, 2s) var(--change-record-animation-easing, ease-in-out);
  width: var(--records-circle-size, 93.6rem);
  height: var(--records-circle-size, 93.6rem);
  margin: 0 0 0 -46.8rem;
  list-style: none;
  position: absolute;
  top: 1.375rem;
  left: 50%;
}

.player__record {
  transform-origin: 50% 46.8rem;
  margin-left: -5.5rem;
  position: absolute;
  left: 50%;
}

.player__record:before {
  content: "";
  transform-origin: center;
  animation: rotation 2s linear var(--tonearm-turn-duration) infinite paused;
  will-change: rotate, scale, box-shadow;
  background: linear-gradient(#000, #333);
  border-radius: 50%;
  width: 11rem;
  height: 11rem;
  transition: all 1.5s;
  display: block;
  box-shadow: 0 0 5px #000000bf;
}

.player__playlist .player__record:first-child {
  transform: rotate(0);
}

.player__playlist .player__record:nth-child(2) {
  transform: rotate(22.5deg);
}

.player__playlist .player__record:nth-child(3) {
  transform: rotate(45deg);
}

.player__playlist .player__record:nth-child(4) {
  transform: rotate(67.5deg);
}

.player__playlist .player__record:nth-child(5) {
  transform: rotate(90deg);
}

.player__playlist .player__record:nth-child(6) {
  transform: rotate(112.5deg);
}

.player__playlist .player__record:nth-child(7) {
  transform: rotate(135deg);
}

.player__playlist .player__record:nth-child(8) {
  transform: rotate(157.5deg);
}

.player__playlist .player__record:nth-child(9) {
  transform: rotate(180deg);
}

.player__playlist .player__record:nth-child(10) {
  transform: rotate(202.5deg);
}

.player__playlist .player__record:nth-child(11) {
  transform: rotate(225deg);
}

.player__playlist .player__record:nth-child(12) {
  transform: rotate(247.5deg);
}

.player__playlist .player__record:nth-child(13) {
  transform: rotate(270deg);
}

.player__playlist .player__record:nth-child(14) {
  transform: rotate(292.5deg);
}

.player__playlist .player__record:nth-child(15) {
  transform: rotate(315deg);
}

.player__playlist .player__record:nth-child(16) {
  transform: rotate(337.5deg);
}

.player_playing .player__record_settled:before {
  animation-play-state: running;
}

.player_changing-record .player__record:before {
  scale: 1.1;
  box-shadow: 0 0 20px #000000bf;
}

@keyframes rotation {
  0% {
    rotate: none;
  }

  100% {
    rotate: 360deg;
  }
}
